import React from 'react';
import { List, ListItem, Box, Stack, Button } from '@mui/material';
// import { Theme } from '@emotion/react';
// import { Route, Routes, useNavigate, Navigate } from 'react-router-dom';
// import api from './utils/api';

function Sidebar() {

    return (
        <Box className="sidebar">
            <Stack spacing={2} className="sidebar__menu">
                <Button className="sidebar__item" variant='primary' size="large">Главная</Button>
                <Button className="sidebar__item" variant='primary' size="large">Таблицы</Button>
                <Button className="sidebar__item" variant='primary' size="large">Графики</Button>
            </Stack>
        </Box>
    );
}

export default Sidebar;
